@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.pyramid {
  padding: 1em;
  background-color: rgb(15, 33, 46);
  width: 420px;
  height: 360px;
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  display: flex;
  /* flex-direction: column-reverse; */
  align-items: center;
}

.multipliers-row {
  display: flex;
  justify-content: center;
  align-items: center;
  /* gap: 0.1em; */
}

.ball-div {
  position: absolute;
  /* top: -2em; */
}

.falling-ball div{
  background: red;
  border-radius: 50%;
  position: absolute;
  /* top: -2em; */
  left: 0;
}

.eight div {
  height: 1.25em;
  width: 1.25em;
}
 
.nine div {
  height: 1.11em;
  width: 1.11em;
}
 
.ten div {
  height: 1em;
  width: 1em;
}
 
.eleven div {
  height: 0.9em;
  width: 0.9em;
}
 
.twelve div {
  height: 0.85em;
  width: 0.85em;
}
 
.thirteen div {
  height: 0.77em;
  width: 0.77em;
}
 
.fourteen div {
  height: 0.72em;
  width: 0.72em;
}
 
.fifteen div {
  height: 0.69em;
  width: 0.69em;
}
 
.sixteen div {
  height: 0.6em;
  width: 0.6em;
}

.hidden-overflow {
  overflow: hidden;
}
.form-menu {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.bet-mode > button {
  width: 50%;
  margin-top: 0;
}

.auto-menu {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.bet-button {
  width: 100%;
  background-color: rgb(16, 248, 47);
  color: #0f212e;
  padding: 0.8em;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border: none;
  /* margin-top: 1em; */
}

.bet-button:hover {
  background-color: rgb(31, 197, 53);
  transition: all 0.2s ease-in-out;
}

.manual-button {
  border-radius: 0.24em 0px 0px 0.24em;
  font-size: 0.8em;
  padding: 0.6em;
}

.auto-button {
  border-radius: 0em 0.24em 0.24em 0em;
  font-size: 0.8em;
  padding: 0.6em;
}

.unselected-tab {
  background-color: rgb(20, 42, 55);
  color: rgb(77, 95, 111);
  border: none;
}

.selected-tab:hover {
  background-color: rgb(66, 90, 108);
  transition: all 0.1s ease-in-out;
}

.selected-tab {
  background-color: rgb(47, 69, 83);
  color: rgb(105, 121, 139);
  cursor: pointer;
  border: none;
  transition: all 0.1s ease-in-out;
}

.set-balance-btn {
  background-color: rgb(49, 109, 236);
  color: none;
  font-size: 0.8em;
  font-weight: 500;
  padding: 0.56em;
  cursor: pointer;
  border-radius: 0.4em;
  border: none;
}

.bal-input {
  display: flex;
  justify-content: space-between;
}

#bal {
  width: 17em;
  
}

.input {
  display: flex;
  flex-direction: column;
  width: 100%;
  grid-gap: 0.4em;
  gap: 0.4em;
}

.bet-modifier {
  min-width: 3.4em;
  background-color: rgb(47, 69, 83);
  box-shadow: none;
  transition: all 0.1s ease-in-out;
}

.bet-modifier:hover {
  background-color: rgb(66, 90, 108);
  border: 2px solid rgb(66, 90, 108);
  transition: all 0.1s ease-in-out;
}

.menu {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  background-color: rgb(33, 55, 67);
  padding: 2em;
  min-width: 24.2em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
}

.balance-section {
  display: flex;
  flex-direction: column;
  grid-gap: 0.4em;
  gap: 0.4em;
  background-color: rgb(33, 55, 67);
  padding: 2em;
  padding-bottom: 0;
  margin-bottom: -1em;
  min-width: 24.2em;
}

#balance {
  background: rgb(20, 42, 55);
  font-weight: 700;
  padding: 0.2em;
  color: rgb(49, 109, 236);
  border-radius: 0.24em;
}

.risk-rows {
  display: flex;
  flex-direction: row;
  grid-gap: 1em;
  gap: 1em;
}

#bet-amount {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  box-shadow: none;
  width: 16.8em;
}

.half {
  border-radius: 0;
  cursor: pointer;
  position: relative;
}

.half::after {
  content: "";
  background: rgb(26, 44, 56);
  position: absolute;
  bottom: 25%;
  right: -2px;
  height: 50%;
  width: 1px;
}

.double {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  cursor: pointer;
  position: relative;
}

.double::after {
  content: "";
  background: rgb(26, 44, 56);
  position: absolute;
  bottom: 25%;
  left: -2px;
  height: 50%;
  width: 1px;
}

.bet-input {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.6);
}

/* 

8 rows
  - width and height of 1.2em
  - gap of 4

16 rows
  - width and height of 0.6em
  - gap of 2em

8  * x = 1.2
16 * x = 0.6
------------
24x = 1.8

*/

html {
  height: 100%;
}

body {
  zoom: 0.8;
  height: 100%;
  width: 100%;
  background-color: rgb(25, 44, 56);
  color: rgb(200, 218, 233);
  font-family: "Work Sans", Arial, Helvetica, sans-serif;
  font-smooth: always;
  display: grid;
  justify-content: center;
  align-items: center;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
}



input,
select,
button {
  padding: 0.2em;
  font-size: 1em;
  font-weight: 600;
  border-radius: 0.24em;
  background-color: rgb(15, 33, 46);
  color: white;
  border: 2px solid rgb(47, 69, 83);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.6);
  transition: all 0.1s ease-in-out;
  font-family: "Work Sans", Arial, Helvetica, sans-serif;
}

select {
  cursor: pointer;
}

input:hover,
select:hover {
  border: 2px solid rgb(85, 112, 134);
  transition: all 0.1s ease-in-out;
}

input:focus,
select:focus {
  outline: none;
  border: 2px solid rgb(85, 112, 134);
}

label {
  font-weight: 500;
}

.error-module {
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 20px;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  body {
    zoom: 1;
  }
  .app {
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
}
.error {
  position: absolute;
  width: 360px;
  height: 44px;
  padding: 30px;
  border-radius: 14px;
  background-color: rgb(236, 105, 54);
}

p {
  margin: 0;
}
